import React from "react";
import { NavLink, useLocation } from "react-router-dom";
import './NavMenu.css';

function NavMenu({ isOpen, isClose, openCalendarPopup, openFormPopup }) {
  const location = useLocation();
  const handleLinkClick=()=> {
    isClose();
    openFormPopup();
  }
  return (
    <section className={isOpen ? `navMenu navMenu_open` : `navMenu`}>
      <nav className="navMenu__container">
        {/* <button type="button" onClick={isClose} className="navMenu__container__close-button hover"></button> */}
        <NavLink to='/' end
          className={({ isActive }) =>
            isActive ? 'link_state_active navMenu__container__link hover' : 'navMenu__container__link hover'}></NavLink>
        <a onClick={handleLinkClick} href='#'className='navMenu__container__link hover' > </a>
        <NavLink to='/5ELEMENT' className={({ isActive }) =>
          isActive ? 'link_state_active navMenu__container__link hover' : 'navMenu__container__link hover'}  >5ELEMENT-  </NavLink>
        <a onClick={isClose} href={location.pathname === "/" ? '#' : '5ELEMENT#'} className='navMenu__container__link hover' ></a>
      </nav>

    </section>
  )
}
export default NavMenu;
